import React from 'react';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  modalRoot: {
    padding: theme.spacing(6),
    textAlign: 'center',
    backgroundColor: theme.palette.primary.main,
    position: 'fixed',
    margin: '0 auto',
    bottom: '20px',
    alignSelf: 'center',
    maxWidth: '90%',
    zIndex: theme.zIndex.modal,
    borderRadius: theme.shape.borderRadius,
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(3),
    },
  },
  paragraph: {
    fontWeight: 200,
  },
  button: {
    backgroundColor: '#292A2C',
    color: theme.palette.text.primary,
    minWidth: '120px',
    '&:hover': {
      backgroundColor: 'black',
    },
  },
}));

const CookiesModal = () => {
  const classes = useStyles();

  return (
    <div id="cookies-modal" className={classes.modalRoot}>
      <Typography className={classes.paragraph} color="textPrimary" paragraph>
        Informacije i suglasnost za kolaie (Cookies) ova web stranica koristi
        kolaie kako bi se poboljalo Vae iskustvo koritenja web stranice i
        pruila uslugu u skladu s Vaim eljama. Postavke kolaia mogu se
        kontrolirati i konfigurirati u na naoj web stranici i u Vaem web
        pretraivau. Ako elite nastaviti koristiti nau web stranicu molimo
        Vas da potvrdite da se slaete s upotrebom kolaia klikom na gumb
        Shvaam.
      </Typography>
      <Button
        variant="contained"
        className={classes.button}
        onClick={() => {
          document.getElementById('cookies-modal').style.display = 'none';
          localStorage.setItem('cookies_seen', JSON.stringify(true));
        }}
      >
        Shvaam
      </Button>
    </div>
  );
};

export default CookiesModal;
